<template>
  <div>
    <CRow class="justify-content-center">
      <CCol md="6">
        <CCard >
          <CCardHeader>
            <strong>Add Thread</strong>
          </CCardHeader>
          <CCardBody>
            <CForm @submit.prevent="addThread" method="post">
              <CInput
                label="Judul Thread"
                placeholder="isi judul thread disini"
                horizontal
                autocomplete="name"
                v-model="thread.thread_title"
                required
              />
              <CSelect
                label="Topik"
                horizontal
                :options="[ { value: 1, label: 'Parenting' }, { value: 2, label: 'Psikologi' }, { value: 3, label: 'Kesehatan' }, { value: 4, label: 'Tips' } ]"
                placeholder="Please select"
                :value.sync="thread.forum_topik_id"
              />
              <vue-editor 
                class="mb-3"
                v-model="thread.thread_content"
                required
               />
              <CButton type="submit" size="sm" color="primary" class="mr-2"><CIcon name="cil-check-circle"/> Submit</CButton>
            </CForm>
          </CCardBody>
          
        </CCard>
        
      </CCol>
      
    </CRow>
  </div>
</template>

<script>
import {VueEditor} from "vue2-editor";
import axios from 'axios';

export default {
  name: 'AddThread',
  components: {
    VueEditor
  },
  data() {
    return {
      thread: {
        thread_title: null,
        thread_content: null,
        forum_topik_id: 0,
      }
    }
  },
  methods: {
    addThread(data){
      this.threads = data;

      let body = {
        admin_user_id : localStorage.getItem('admin_id'),
        thread_title: this.thread.thread_title,
        thread_content: this.thread.thread_content,
        forum_topik_id: this.thread.forum_topik_id,
      };

      const result = 
        axios
        .post('http://103.151.215.74/api/admin/forum/thread/add', body, {
          headers: {
            Authorization: localStorage.getItem('token')
          }
        })
        .then(
          (result) => {
            // console.log("Berhasil : ", result);
            this.$router.push('/menus/forum');
          }
        )
        // .catch(function (error) {
        //   // handle error
        //   // console.log("Gagal : ", error);
        // });
    }
  },
}
</script>